@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@300..800&family=Poppins:wght@600;700&display=swap";
html {
  scroll-behavior: smooth;
}

.container {
  padding-inline: 20px;
}

@media screen and (width >= 640px) {
  .container {
    padding-inline: 41px;
  }
}

@media screen and (width >= 1024px) {
  .container {
    padding-inline: 54px;
  }
}

@media screen and (width >= 1600px) {
  .container {
    box-sizing: border-box;
    max-width: 1600px;
    margin-inline: auto;
    padding-inline: 227px;
  }
}

h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  background: #eee;
  min-width: 320px;
  font-family: Open sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  pointer-events: none;
  z-index: 20;
  background-color: #2060f6;
  border-radius: 0 0 30px 30px;
  width: 100%;
  height: 100vh;
  padding-block: 21px;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: -100%;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  left: 0;
}

.header {
  color: #fff;
  box-sizing: border-box;
  flex-direction: column;
  max-width: 1600px;
  height: 100vh;
  margin-inline: auto;
  padding-top: 20px;
  transition: all .3s;
  display: flex;
}

@media screen and (width >= 1024px) {
  .header {
    padding-top: 48px;
  }
}

.header--dark {
  background: #2c2c2c;
  border-radius: 0 0 30px 30px;
}

.header__top-bar {
  margin-bottom: 48px;
}

.header__bottom {
  color: #fff;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding-inline: 20px;
  display: flex;
  position: relative;
}

@media screen and (width >= 640px) {
  .header__bottom {
    padding-inline: 41px;
  }
}

@media screen and (width >= 1024px) {
  .header__bottom {
    padding-inline: 54px;
  }
}

@media screen and (width >= 1600px) {
  .header__bottom {
    box-sizing: border-box;
    max-width: 1600px;
    margin-inline: auto;
    padding-inline: 227px;
  }
}

@media screen and (width >= 1024px) {
  .header__bottom {
    justify-content: center;
  }
}

@media screen and (width >= 1600px) {
  .header__bottom {
    margin-inline: 0;
  }
}

.header__slider {
  border-radius: 30px;
  height: 390px;
  transition: border-radius .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (width >= 1024px) {
  .header__slider {
    border-radius: 30px 0;
    width: 50%;
    height: 100%;
    left: auto;
  }
}

.top-bar {
  flex-direction: row;
  align-items: center;
  padding-inline: 20px;
  display: flex;
}

@media screen and (width >= 640px) {
  .top-bar {
    padding-inline: 41px;
  }
}

@media screen and (width >= 1024px) {
