    margin-bottom: 56px;
  }
}

@media screen and (width >= 1024px) {
  .top-bar {
    justify-content: space-between;
    margin-bottom: 56px;
  }
}

@media screen and (width >= 1600px) {
  .top-bar {
    margin-inline: 0;
  }
}

.top-bar__menu {
  display: none;
}

@media screen and (width >= 1024px) {
  .top-bar__menu {
    display: block;
  }
}

.top-bar__logo {
  cursor: pointer;
  height: 28px;
  margin-right: 32px;
  transition: transform .3s;
}

@media screen and (width >= 1024px) {
  .top-bar__logo {
    margin: 0;
  }
}

.top-bar__logo:hover {
  transform: scale(1.2);
}

.top-bar__icon-menu {
  width: 24px;
  height: 24px;
  display: block;
  position: relative;
}

.top-bar__icon-menu:after, .top-bar__icon-menu:before {
  content: "";
  transform-origin: 100%;
  background-color: #fff;
  width: 18px;
  height: 2px;
  transition: transform .3s;
  position: absolute;
  transform: scale(1);
}

.top-bar__icon-menu:after {
  top: 8px;
}

.top-bar__icon-menu:before {
  bottom: 8px;
}

.top-bar__icon-menu:hover:after {
  transform: scaleX(.8);
}

@media screen and (width >= 1024px) {
  .top-bar__icon-menu {
    display: none;
  }
}

.top-bar__right {
  align-items: center;
  gap: 20px;
  margin-left: auto;
  display: flex;
}

@media screen and (width >= 1024px) {
  .top-bar__right {
    margin: 0;
  }
}

.nav__list {
  gap: 48px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list--burger {
  flex-direction: column;
}

.nav__link {
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  position: relative;
}

.nav__link:after {
  content: "";
  transform-origin: center;
  background: #2060f6;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.nav__link:hover:after {
  transform: scale(1);
}

.icon {
  width: 24px;
  height: 24px;
  margin: 0;
  display: block;
}

.icon--menu {
  background-image: url("icon-menu.ff4e3478.svg");
  margin-left: 32px;
}

@media screen and (width >= 1024px) {
  .icon--menu {
    display: none;
  }
}

.icon--close {
  background-image: url("icon-close.a491bbae.svg");
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (width >= 1024px) {
  .burger-menu {
    display: none;
  }
}

.burger-menu_button {
  width: 24px;
  height: 24px;
  transition: all .3s;
  display: block;
  position: relative;
}

.burger-menu_button:after, .burger-menu_button:before {
  content: "";
  background-color: #fff;
  width: 18px;
  height: 2px;
  position: absolute;
}

.burger-menu_button:after {
  width: 20px;
  height: 4px;
  top: 50%;
  transform: rotate(45deg);
}

.burger-menu_button:before {
  width: 20px;
  height: 4px;
  top: 50%;
  transform: rotate(-45deg);
}

.burger-menu_button:hover {
  transform: scale(1.2);
}

.burger-menu__top {
  z-index: 1;
  align-items: center;
  margin-bottom: 32px;
  display: flex;
  position: sticky;
  top: 0;
}

.burger-menu__nav-list {
  flex-direction: column;
  gap: 50px;
  padding-block: 60px;
  list-style-type: none;
  display: flex;
  position: relative;
}

.burger-menu__nav-item {
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  position: relative;
}

.burger-menu__nav-item:after {
  content: "";
  transform-origin: center;
  background: #fff;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.burger-menu__nav-item:hover:after {
  transform: scale(1);
}

